<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import vueAmx from 'vue-amx'
import Vue from 'vue'
let appNet = {
  name: 'appNet',
  type:false,
  store:{
    type:'none'
  }
}
let store = [appNet]
Vue.use(vueAmx,store)
export default {
  name: "App"
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
</style>



