import Vue from 'vue'
import Router from 'vue-router'

import carshow from '@/carshow/Headerb'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/carshow/',
      name: 'carshow',
      component: carshow
    }
  ]
})



